*,*::after,*::before{margin:0;padding:0;box-sizing:inherit}body{background-color:#212121;margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.App{display:flex;flex-direction:column;font-size:calc(3px + 2vmin);color:#fff;max-width:98vw;margin:0 15px 0 15px}.codeBlockContent{background-color:#545454;padding:25px;border:1px solid #ccc;border-radius:10px}.codeBlockContent pre{white-space:pre-wrap;white-space:-moz-pre-wrap;white-space:-o-pre-wrap;word-wrap:break-word;font-family:"Courier New", serif}.langSelection{display:flex;flex-direction:row-reverse;align-items:center;padding:15px 10px 0 0}.langSelection select,.langSelection img{margin-right:10px}header{background-position:bottom;background-size:cover;background-repeat:no-repeat;height:50vh}header h1{text-align:center;padding:0 0 30px 0;font-family:"Lucida Console", serif;font-size:3em;margin-top:10vh}section{margin-top:15px;padding:30px;background-color:#3b3b3b}footer{color:#ccc;background-color:#3b3b3b;margin-top:15px;padding:15px 0 15px 0;text-align:center}
